}

.welcomeText {
  margin-top: 20px;
  font-size: 1.8rem;
  font-weight: bold;
  color: #495057;
}

/* --- ì˜¤ë¥¸ìª½ (í¼ ì„¹ì…˜) --- */
.rightSection {
  flex: 1;
  padding: 3rem 2rem;
  display: flex;
  flex-direction: column;
  position: relative;
}

.closeButton {
  position: absolute;
  top: 15px;
  right: 20px;
  background: none;
  border: none;
  font-size: 1.5rem;
  cursor: pointer;
  color: #868e96;
}

.title {
  font-size: 1.5rem;
  font-weight: bold;
  margin-bottom: 2rem;
}

.subTitle {
  font-size: 0.9rem;
  color: #868e96;
  margin-bottom: 10px;
}

/* ì…ë ¥ í¼ ìŠ¤íƒ€ì¼ */
.form {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-bottom: 2rem;
}

.input {
  padding: 12px;
  border: 1px solid #dee2e6;
  border-radius: 4px;
  font-size: 1rem;
}

.loginButton {
  background-color: #12b886; /* ìŠ¤í¬ë¦°ìƒ·ì˜ ì´ˆë¡ìƒ‰ */
