/* styles/chatService/ChatRoom.module.css */

.container {
  display: flex;
  flex-direction: column;
  height: 100%;
  max-height: 100vh;
  background: #f9fafb;
  position: relative;
  overflow: hidden;
}

.header {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 12px 16px;
  background: white;
  border-bottom: 1px solid #e5e7eb;
  position: relative;
  z-index: 10;
  flex-shrink: 0;
}

.backBtn {
  padding: 8px;
  border-radius: 50%;
  background: none;
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background 0.2s;
}

.backBtn:hover {
  background: #f3f4f6;
}

@media (min-width: 768px) {
  .backBtn {
    display: none;
  }
}

.roomName {
  font-size: 1.125rem;
  font-weight: 600;
  color: #111827;
  flex: 1;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.connectionStatus {
  margin-left: auto;
  display: flex;
  align-items: center;
  gap: 8px;
}

.statusDot {
  width: 8px;
  height: 8px;
  border-radius: 50%;
}

.statusDot.connected {
  background: #10b981;
}

.statusDot.disconnected {
  background: #ef4444;
}

.statusText {
  font-size: 0.75rem;
  color: #6b7280;
}

.messagesArea {
  flex: 1;
  overflow-y: auto;
  overflow-x: hidden;
  padding: 24px 16px;
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.loadingMore {
  text-align: center;
  padding: 12px;
  color: #6b7280;
  font-size: 0.875rem;
  background: #f9fafb;
  border-radius: 8px;
  margin-bottom: 16px;
}

.loadingMessage,
.emptyMessage {
  text-align: center;
  color: #6b7280;
  padding: 40px 20px;
}

.messageRow {
  display: flex;
}

.messageRow.mine {
  justify-content: flex-end;
}

.messageRow.theirs {
  justify-content: flex-start;
}

.messageContent {
  display: flex;
  flex-direction: column;
  max-width: 80%;
}

.messageContent.mine {
  align-items: flex-end;
}

.messageContent.theirs {
  align-items: flex-start;
}

.senderName {
  font-size: 0.75rem;
  font-weight: 500;
  color: #6b7280;
  margin-bottom: 4px;
  padding: 0 4px;
}

.messageBubble {
  padding: 12px 16px;
  border-radius: 16px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);
  word-wrap: break-word;
  word-break: break-word;
}

.messageBubble.mine {
  background: #3b82f6;
  color: white;
  border-bottom-right-radius: 4px;
}

.messageBubble.theirs {
  background: white;
  color: #111827;
  border: 1px solid #e5e7eb;
  border-bottom-left-radius: 4px;
}

.messageTime {
  font-size: 0.75rem;
  color: #9ca3af;
  margin-top: 4px;
  padding: 0 4px;
}

.messageFooter {
  display: flex;
  align-items: center;
  gap: 6px;
  margin-top: 4px;
  padding: 0 4px;
}

.readStatus {
  font-size: 0.75rem;
  color: #ef4444;
  font-weight: 600;
  min-width: 12px;
}

.inputArea {
  border-top: 1px solid #e5e7eb;
  background: white;
  padding: 16px;
  flex-shrink: 0;
  position: relative;
  z-index: 10;
}

.inputContainer {
  display: flex;
  align-items: center;
  gap: 12px;
  max-width: 1024px;
  margin: 0 auto;
}

.messageInput {
  flex: 1;
  padding: 12px 20px;
  font-size: 1rem;
  border: 1px solid #d1d5db;
  border-radius: 24px;
  outline: none;
  transition: all 0.2s;
}

.messageInput:focus {
  border-color: #3b82f6;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

.messageInput:disabled {
  background: #f3f4f6;
  cursor: not-allowed;
}

.sendBtn {
  padding: 12px;
  background: #3b82f6;
  color: white;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s;
  box-shadow: 0 4px 6px rgba(59, 130, 246, 0.3);
}

.sendBtn:hover {
  background: #2563eb;
  transform: scale(1.05);
}

.sendBtn:disabled {
  background: #d1d5db;
  cursor: not-allowed;
  box-shadow: none;
  transform: none;
}

.debugInfo {
  margin-top: 8px;
  padding: 8px;
  background: #f3f4f6;
  border-radius: 8px;
  font-size: 0.75rem;
  color: #6b7280;
}

.debugInfo div {
  margin: 2px 0;
}

@media (max-width: 640px) {
  .messageContent {
    max-width: 85%;
  }

  .roomName {
    max-width: 200px;
  }
}